.bottomMenu {
    display: none;
    @media screen and (max-width: 768px) {
        display: flex;
    }
    padding: 0px 10px 12px 10px;
    width: 100%;
    height: 72px;
    position: fixed;
    bottom: 0;
    left: 0;
    align-items: center;
    justify-content: space-around;
    background-color: var(--white);
    z-index: 10;
    border-top-left-radius: 26px;
    border-top-right-radius: 26px;
    box-shadow: 0px -4px 12px 0px rgba(0, 0, 0, 0.06);
    overflow: hidden;
    &_item {
        height: 60px;
        width: 64px;
        background-color: var(--white);
        border: none;
        outline: none;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 18px;
        border-top: 2px solid var(--white);
        color: var(--gray-dark);
        text-align: center;
        font-family: "Inter-Regular";
        font-weight: 400;
        font-size: 12px;
        line-height: 140%;
        white-space: nowrap;
        img {
            height: 25px;
            margin-bottom: 4px;
        }
        &_notificationContainer {
            position: relative;
            padding: 2px;
            min-height: 24px;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-bottom: 4px;
            img {
                height: 19px;
                margin-bottom: 0;
            }
        }
        &_notification {
            position: absolute;
            top: -7px;
            right: -7px;
            width: 18px;
            height: 18px;
            background-color: var(--red);
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            color: var(--white);
            font-family: "Cairo";
            font-weight: 700;
            font-size: 15px;
            text-align: center;
            line-height: 100%;
        }
        &_selected {
            border-top: 2px solid var(--green-dark2);
            color: var(--green-dark2);
        }
    }
}

.newRequestMenuContainer {
    width: 100%;
    height: 100%;

    position: relative;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: var(--popUpBaseBackground);
    backdrop-filter: blur(2.5px);
    z-index: 10;
    @keyframes slideUp {
        from {
            bottom: -390px;
        }
        to {
            bottom: 0;
        }
    }

    &_newRequestMenu {
        width: 100%;
        height: 390px;
        display: flex;
        justify-content: center;
        align-items: center;

        bottom: -390px;
        left: 0;
        border-radius: 20px 20px 0px 0px;
        background: var(--gray-light);
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 12px 16px 36px 16px;
        transition-duration: 0.3s;
        &_line {
            width: 80px;
            min-height: 5px;
            border-radius: 45px;
            background: var(--gray-light3);
            margin-bottom: 18px;
        }
        &_title {
            color: var(--gray-dark);
            font-family: "Poppins";
            font-weight: 400;
            font-weight: 500;
            font-size: 20px;
            margin-bottom: 12px;
        }
        &_item {
            width: 100%;
            height: 50px;
            background-color: var(--white);
            border-radius: 8px;
            margin-bottom: 8px;
            padding: 12px;
            display: flex;
            align-items: center;
            color: var(--gray);
            font-family: "Poppins";
            font-weight: 400;
            font-weight: 500;
            font-size: 16px;
            line-height: normal;
            &_title {
                display: flex;
                padding: 4px 6px;
                justify-content: center;
                align-items: center;
                border-radius: 12px;
                border: 2px solid var(--green-dark2);
                color: var(--black);
                font-family: "Poppins";
                font-weight: 400;
                font-weight: 500;
                font-size: 12px;
                line-height: 10px;
                margin-left: 6px;
            }
            img {
                margin-right: 6px;
            }
        }
    }
    &_newRequestMenuAnim {
        animation: slideUp 0.3s forwards;
    }
}

.BottomSheet__window-wrap {
    border-radius: 20px 20px 0px 0px !important;
}
