.welcomeContainer {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    background-color: var(--gray-light);
    overflow-x: hidden;
    &__content {
        flex: 1 1 auto;
        width: 100%;
        display: flex;
        position: relative;
        &_main {
            flex: 1;
            padding: 24px 24px 0px 24px;
            width: 74%;

            @media screen and (max-width: 1024px) {
                width: 65%;
            }
            @media screen and (max-width: 768px) {
                padding: 18px 16px 72px 16px;
            }
            &_creditUsage {
                width: 100%;
                height: 217px;
                margin-bottom: 24px;
                @media screen and (max-width: 1440px) {
                    height: 166px;
                }
                @media screen and (max-width: 1024px) {
                    height: 138px;
                }
                @media screen and (max-width: 768px) {
                    margin-bottom: 16px;
                }
                &_text {
                    color: var(--black);
                    font-family: "Poppins";
                    font-weight: 400;
                    font-weight: 500;
                    font-size: 32px;
                    line-height: 100%;
                    margin-bottom: 18px;
                    @media screen and (max-width: 1440px) {
                        font-size: 32px;
                        margin-bottom: 12px;
                    }
                    @media screen and (max-width: 1024px) {
                        font-size: 22px;
                        margin-bottom: 8px;
                    }
                }
                &_cardsList {
                    display: flex;
                    align-items: center;
                    justify-content: flex-start;
                    height: 161px;
                    @media screen and (max-width: 1440px) {
                        height: 120px;
                    }
                    @media screen and (max-width: 1024px) {
                        height: 108px;
                    }
                    @media screen and (max-width: 768px) {
                        justify-content: space-between;
                    }
                    &_card {
                        margin-right: 15px;

                        height: 100%;
                        background-color: var(--white);
                        padding: 24px;
                        display: flex;
                        flex-direction: column;
                        align-items: flex-start;
                        border-radius: 16px;
                        padding-right: 30px;
                        @media screen and (max-width: 1440px) {
                            padding: 12px;
                        }
                        @media screen and (max-width: 768px) {
                            min-width: unset;
                            padding: 8px 12px;
                            margin-right: 0px;
                            width: 31.7%;
                        }
                        &_header {
                            font-family: "Poppins";
                            font-weight: 400;
                            font-weight: 500;
                            font-size: 20px;
                            font-weight: 500;
                            line-height: 22px;
                            margin-bottom: 8px;
                            color: var(--black);

                            @media screen and (max-width: 1440px) {
                                font-family: "Poppins";
                                font-weight: 600;
                                font-size: 18px;
                                line-height: 120%;
                                margin-bottom: 4px;
                                white-space: nowrap;
                            }
                            @media screen and (max-width: 1024px) {
                                font-size: 16px;
                            }
                            @media screen and (max-width: 768px) {
                                white-space: unset;
                                font-size: 14px;
                                width: 75px;
                            }
                        }
                        &_text {
                            color: var(--gray-dark);
                            font-family: "Inter-Regular";
                            font-weight: 400;
                            font-size: 16px;
                            line-height: 120%;
                            display: flex;
                            align-items: center;
                            margin-bottom: 24px;

                            @media screen and (max-width: 1440px) {
                                font-size: 16px;
                                margin-bottom: 12px;
                            }
                            @media screen and (max-width: 1024px) {
                                margin-bottom: 8px;
                            }
                            @media screen and (max-width: 768px) {
                                font-size: 12px;
                            }

                            &_iconContainer {
                                position: relative;
                                margin-left: 4px;
                                cursor: pointer;
                                display: flex;
                                &_hoverText {
                                    position: absolute;
                                    background-color: var(--white);

                                    width: 332px;
                                    height: 112px;
                                    box-shadow: 5px 4px 10px 0px rgba(90, 95, 101, 0.1);
                                    padding: 12px;
                                    border-radius: 8px;
                                    z-index: 10;
                                    @media screen and (max-width: 1440px) {
                                        width: 288px;
                                        height: 109px;
                                    }
                                    &_header {
                                        color: var(--gray-dark);
                                        font-family: "Poppins";
                                        font-weight: 400;
                                        font-weight: 500;
                                        font-size: 16px;
                                        line-height: normal;
                                        display: flex;
                                        align-items: center;
                                        @media screen and (max-width: 1440px) {
                                            font-size: 16px;
                                        }
                                        img {
                                            margin-right: 6px;
                                        }
                                    }
                                    &_title {
                                        color: var(--gray);
                                        font-family: "Inter-Regular";
                                        font-weight: 400;
                                        font-size: 14px;
                                        line-height: 120%;
                                        @media screen and (max-width: 1440px) {
                                            font-size: 14px;
                                        }
                                    }
                                }
                            }
                        }
                        &_number {
                            color: var(--purple);
                            font-family: "Poppins";
                            font-weight: 700;
                            font-size: 42px;
                            line-height: 100%;

                            @media screen and (max-width: 768px) {
                                font-size: 30px;
                                font-family: "Poppins";
                                font-weight: 700;
                            }
                        }
                    }
                }
