                                align-items: center;
                                background: var(--gray-light2);
                                outline: none;
                                border: none;
                                padding: 3px;
                                border-radius: 50%;
                                background-image: url(../../assets/images/Welcome/NavigationArrows/LightArrow.svg);
                                background-position: center;
