                            padding: 8px 12px 8px 21px;
                        }
                        &_header {
                            color: var(--black);
                            display: flex;
                            align-items: center;
                            justify-content: space-between;
                            min-height: 40px;
                            margin-bottom: 8px;
                            width: 100%;
                            overflow-wrap: break-word;
                            font-family: "Poppins";
                            font-weight: 400;
                            font-weight: 500;
                            font-size: 20px;
                            font-weight: 500;
                            line-height: 22px;
                            text-align: left;

                            @media screen and (max-width: 1440px) {
                                font-family: "Poppins";
                                font-weight: 600;
                                font-size: 18px;
                                line-height: 18px;
                                min-height: 30px;
                            }
                            @media screen and (max-width: 1024px) {
                                min-height: 20px;
                                margin-bottom: 8px;
                                font-size: 18px;
                            }
                            @media screen and (max-width: 768px) {
                                font-size: 14px;
                                min-height: 32px;

                                margin-bottom: 0px;
                            }
                            &_status {
                                display: flex;
                                align-items: center;
                                color: var(--gray-dark);

                                font-family: "Poppins";
                                font-weight: 400;
                                font-weight: 500;
                                font-size: 14px;
                                @media screen and (max-width: 1440px) {
                                    font-size: 14px;
                                }
                                @media screen and (max-width: 1024px) {
                                    margin-left: 6px;
                                }
                                @media screen and (max-width: 768px) {
                                    font-size: 12px;
                                }
                                &_scheduled {
                                    background-image: url(../../assets/images/Projects/ScheduledIcon.svg);
                                    background-size: 100% 100%;
                                    background-repeat: no-repeat;
                                    background-position: center;
                                    width: 20px;
                                    height: 20px;
                                    margin-right: 4px;
                                }
                                &_pending {
                                    background-image: url(../../assets/images/Projects/PendingIcon.svg);
                                    background-size: 100% 100%;
                                    background-repeat: no-repeat;
                                    background-position: center;
                                    width: 20px;
                                    height: 20px;
                                    margin-right: 4px;
                                }
                            }
                        }
                        &_text {
                            color: var(--gray-dark);
                            margin-bottom: 9px;
                            display: flex;
                            align-items: center;
                            justify-content: start;

                            font-family: "Inter-Regular";
                            font-weight: 400;
                            font-size: 18px;
                            font-weight: 400;
                            line-height: 19.8px;
                            text-align: left;

                            @media screen and (max-width: 1440px) {
                                font-family: "Poppins";
                                font-weight: 400;
                                font-size: 18px;
                                line-height: 20px;
                                height: 20px;
                                margin-bottom: 10px;
                            }

                            @media screen and (max-width: 1024px) {
                                height: 15px;
                                font-size: 16px;
                                line-height: 14px;
                                margin-bottom: 8px;
                            }
                            @media screen and (max-width: 768px) {
                                font-size: 14px;
                                height: unset;
                                margin-bottom: 7px;
                            }
                            &_info {
                                display: flex;
                                padding: 6px 8px;
                                justify-content: center;
                                align-items: center;
                                border-radius: 35px;
                                border: 2px solid var(--green-dark2);
                                color: var(--black);
                                font-family: "Poppins";
                                font-weight: 400;
                                font-weight: 500;

                                margin-left: 12px;

                                font-size: 14px;
                                line-height: 14px;

                                @media screen and (max-width: 1440px) {
                                    font-size: 14px;
                                    line-height: 14px;
                                }
                                @media screen and (max-width: 1024px) {
                                    display: flex;
                                    padding: 4px 6px;
                                    justify-content: center;
                                    align-items: center;
                                    font-size: 12px;
                                }
                            }
                        }
                        &_subText {
                            color: var(--gray-dark);
                            height: 20px;
                            margin-bottom: 4px;
                            display: flex;
                            align-items: center;
                            justify-content: start;

                            font-family: "Inter-Regular";
