                                display: flex;
                                padding: 6px 8px;
                                justify-content: center;
                                align-items: center;
                                border-radius: 12px;
                                background-color: var(--green);
                                color: var(--white);
                                font-family: "Poppins";
                                font-weight: 400;
                                font-weight: 500;
                                font-size: 14px;
                                line-height: 12px;
                                margin-left: 12px;
                                @media screen and (max-width: 1024px) {
                                    display: flex;
                                    padding: 4px 6px;
                                    justify-content: center;
                                    align-items: center;

                                    font-size: 12px;
                                    border-radius: 12px;
                                }
                            }
                        }
                        &_line {
                            width: 3px;
                            height: 128px;
                            border-radius: 40px;
                            background-color: var(--green-dark2);
                            left: 14px;
                            top: 50%;
                            position: absolute;
                            transform: translateY(-50%);
                            @media screen and (max-width: 1440px) {
                                left: 16px;
                                height: 112px;
                            }
                            @media screen and (max-width: 1024px) {
                                height: 98px;
                            }
                            @media screen and (max-width: 768px) {
                                left: 12px;
                                height: 93px;
                            }
                        }
                    }
                    &_newRequest {
                        width: 368px;
                        height: 100%;
                        border-radius: 18px;
                        background-color: var(--white-light);
                        margin-right: 16px;
                        padding: 10px 16px 16px 27px;
                        position: relative;
                        display: flex !important;
                        flex-direction: column;
                        align-items: center;
                        justify-content: center;
                        color: var(--green-dark);
                        img {
                            width: 43px;
