                            line-height: 17.6px;
                            letter-spacing: 0.02em;
                            text-align: left;
                        }
                        @media screen and (max-width: 1024px) {
                            width: 338px;
                            padding: 11px 16px 16px 27px;
                        }
                        @media screen and (max-width: 768px) {
                            padding: 8px 12px 8px 21px;
