                                width: 32.4px;
                                height: 32.4px;
                                margin-bottom: 6px;
                            }
                            //styleName: Heading/H6;
                            font-family: "Poppins";
                            font-weight: 400;
                            font-weight: 500;
                            font-size: 12px;
                            font-weight: 500;
                            line-height: 13.2px;
                            text-align: left;
                        }
