.nR {
    &_container {
        width: 100%;
        height: max-content;
    }

    &_header {
        padding: 24px 24px 0;
        position: relative;
        &_colorSwitcher {
            max-width: 30px;
            max-height: 30px;
            width: 30px;
            height: 30px;
            border-radius: 5px;
            position: relative;
            position: absolute;
            top: 20px;
            right: 20px;
            border: 2px solid var(--green-dark2);
            cursor: pointer;

            overflow: visible;

            span {
                font-size: 9px;
                position: absolute;
                bottom: -16px;
                left: -8px;
            }
        }
        &_text {
            font-family: "Poppins";
            font-size: 28px;
            font-weight: 500;
            line-height: 33.6px;
            text-align: left;
            margin-bottom: 12px;
            color: var(--black);
        }
        &_subText {
            font-family: "Inter-SemiBold";
            font-size: 16px;
            font-weight: 400;
            line-height: 19.2px;
            text-align: left;
            color: var(--gray);
            margin-bottom: 24px;
        }
        &_info {
            display: flex;
            align-items: center;
            justify-content: space-between;
            font-family: "Inter-Regular";
            font-size: 16px;
            font-weight: 400;
            line-height: 19.2px;
            color: var(--gray-dark);
            text-align: left;
            padding-bottom: 15px;
            span {
                font-family: "Poppins";
                font-size: 16px;
                font-weight: 500;
                line-height: 17.6px;
                letter-spacing: 0.02em;
                text-align: left;
                color: var(--black);
            }
        }
        margin-bottom: 24px;
    }
    &_content {
        display: flex;
        gap: 16px;
        padding: 24px 24px 0px 24px;
    }
    &_formContainer {
        flex: 1;
        background-color: var(--white);
        border-radius: 16px;
        padding: 16px 24px;
        &_buttons {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-top: 16px;
            margin-bottom: 36px;
            text-decoration: none;
            a {
                text-decoration: none;
            }
            &_next {
                width: 237px;
                height: 47px;
                border-radius: 10px;
                background-color: var(--gray-light3);
                color: var(--white);
                //styleName: Heading/H3;
                font-family: Poppins;
                font-size: 18px;
                font-weight: 600;
                line-height: 27px;
                text-align: center;
                display: flex;
                align-items: center;
                justify-content: center;
                cursor: pointer;
            }
            &_back {
                width: 193px;
                height: 47px;
                display: flex;
                align-items: center;
                justify-content: center;
                gap: 6px;
                border-radius: 10px;
                border: 2px solid var(--gray-light3);
                font-family: Poppins;
                font-size: 18px;
                font-weight: 600;
                line-height: 27px;
                text-align: center;
                color: var(--gray-dark);
                cursor: pointer;
            }
        }
        &_error {
            //styleName: Body/Content Regular;
            font-family: "Inter-Regular";
            font-size: 16px;
            font-weight: 400;
            line-height: 19.2px;
            text-align: left;
            color: var(--red-dark);
            background-color: var(--red-light);
            padding: 10px 18px;
            border-radius: 10px;
        }
    }
    &_question {
        display: flex;
        align-items: flex-start;
        flex-direction: column;
        &_header {
            &_text {
                font-family: Poppins;
                font-size: 16px;
                font-weight: 700;
                line-height: 17.6px;
                letter-spacing: 0.02em;
                text-align: left;
                margin-bottom: 4px;
                font-family: Poppins;
                font-size: 18px;
                font-weight: 600;
                line-height: 27px;
                text-align: left;
                color: var(--black);

