                    color: var(--red);
                }
            }
            &_subText {
                font-family: "Inter-Regular";
                font-size: 14px;
                font-weight: 400;
                line-height: 16.8px;
                text-align: left;
                color: var(--blue-dark);
                display: flex;
                align-items: center;
                cursor: pointer;

                font-family: "Inter-Medium";
                font-size: 16px;
                font-weight: 400;
                line-height: 17.8px;
                text-align: left;
                color: var(--blue-dark);
