.nR {
    display: flex;
    align-items: center;
    flex-direction: column;
    height: 100%;
    &_container {
        display: flex;
        flex-direction: column;
        width: 100%;
        flex: 1px;
    }

    &_header {
        &_text {
            font-family: "Poppins";
            font-size: 28px;
            font-weight: 500;
            line-height: 33.6px;
            text-align: left;
            margin-bottom: 12px;
            color: var(--black);
        }
        &_subText {
            font-family: "Inter-SemiBold";
            font-size: 16px;
            font-weight: 400;
            line-height: 19.2px;
            text-align: left;
            color: var(--gray);
            margin-bottom: 24px;
        }
        &_info {
            display: flex;
            align-items: center;
            justify-content: space-between;
            font-family: "Inter-Regular";
            font-size: 16px;
            font-weight: 400;
            line-height: 19.2px;
            color: var(--gray-dark);
            text-align: left;
            padding-bottom: 15px;
            border-bottom: 1px solid var(--gray-light3);
            span {
                font-family: "Poppins";
                font-size: 16px;
                font-weight: 500;
                line-height: 17.6px;
                letter-spacing: 0.02em;
                text-align: left;
                color: var(--black);
            }
        }
        margin-bottom: 24px;
    }
    &_content {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        gap: 20px;
        width: 100%;
        &_card {
            min-width: 360px;
            height: 160px;
            max-width: 360px;
            border-radius: 16px;
            background-color: var(--white);
            display: flex;
            align-items: flex-start;
            flex-direction: column;
            padding: 16px;
            cursor: pointer;
            transition-duration: 0.3s;
            position: relative;
            &_header {
                width: 100%;
                display: flex;
                align-items: center;
                //styleName: Heading/H4;
                font-family: Poppins;
                font-size: 16px;
                font-weight: 600;
                line-height: 17.6px;
                letter-spacing: 0.02em;
                text-align: left;
                color: var(--black);
                margin-bottom: 12px;
                min-height: 28px;
                img{
                    margin-right: 8px;
                }
            }
            &_divider {
                width: 100%;
                min-height: 1px;
                height: 1px;
                background-color: var(--gray-light3);
                margin-bottom: 12px;
            }
            &_text {
                //styleName: Text/Medium;
                font-family: "Inter-Regular";
                font-size: 16px;
                font-weight: 400;
                line-height: 19.2px;
                text-align: left;
                color: var(--gray-dark);
                margin-bottom: 12px;
            }
            &_title {
                display: flex;
                padding: 4px 6px;
                justify-content: center;
                align-items: center;
                border-radius: 12px;
                border: 2px solid var(--green-dark2);
                color: var(--black);
                font-family: "Poppins";
                font-weight: 400;
                font-weight: 500;
                font-size: 12px;
                line-height: 10px;
            }
            &_arrow{
                transform: rotate(-90deg);
                position: absolute;
                top:20px;
                right:16px;
            }
        }
        &_card:hover {
            transform: scale(1.05);
        }
    }
}
