.popUp {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    backdrop-filter: blur(2.5px);
    background: rgba(17, 16, 14, 0.6);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 10;
    &_content {
        width: 720px;
        min-height: 735px;
        border-radius: 20px;
        background-color: var(--white);
        padding: 24px 48px 48px 48px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        position: relative;
        &_info {
            width: 100%;
            height: 36px;
            font-family: "Inter-Regular";
            font-size: 14px;
            font-weight: 400;
            line-height: 16.8px;
            text-align: left;
            color: var(--gray);
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-top: 8px;
            img{
                cursor: pointer;
                margin-left: 8px;
            }
            div{
                display: flex;
                align-items: center;
            }
            &_divider {
                min-width: 4px;
                min-height: 4px;
                border-radius: 50%;
                background-color: var(--gray-light5);
                margin-left: 6px;
                margin-right: 6px;
                display: inline-block;
                width:4px;
                height:4px;
            }
            &_date {
                color: var(--gray-dark);
                margin-left: 8px;
            }
            &_time{
                color: var(--gray-dark);
            }
        }
        &_dateTime {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 16px;
        }
        &_timeSelector {
            width: 194px;
            height: 412px;
            border: 1px solid var(--gray-light3);
            border-radius: 16px;
            display: flex;
            flex-direction: column;
            align-items: center;
            &_header {
                height: 64px;
                display: flex;
                align-items: center;
                justify-content: center;
                //styleName: Body/Hero Accent;
                font-family: "Inter-Medium";
                font-size: 20px;
                font-weight: 500;
                line-height: 24px;
                text-align: left;
                color: var(--gray-dark);
                border-bottom: 1px solid var(--gray-light3);
                width: 100%;
            }
            &_list {
                flex: 1;
                display: flex;
                align-items: center;
                flex-direction: column;
                overflow: scroll;
                width: 100%;
                padding: 16px;
                &_item {
                    height: 36px;
                    min-height: 36px;
                    width: 100%;
                    display: flex;
                    align-items: center;
                    gap: 6px;
                    justify-content: flex-start;
                    padding-left: 24px;
                    font-family: Poppins;
                    font-size: 16px;
                    font-weight: 500;
                    line-height: 16px;
                    letter-spacing: 0.02em;
                    text-align: left;
                    margin-right: 6px;
                    color: var(--gray);
                    img {
                        margin-right: 8px;
                    }
                    cursor: pointer;
                    &_notAvailable {
                        cursor: not-allowed;
                        opacity: 0.5;
                    }
                    &_selected {
                        background-color: var(--blue-light);
                        //styleName: Numbers/Time Calendar Bold;
                        font-family: Poppins;
                        font-size: 16px;
                        font-weight: 700;
                        line-height: 16px;
                        letter-spacing: 0.02em;
                        text-align: left;
                        color: var(--black) !important;
                    }
                }
                &_item:hover {
                    background-color: var(--gray-light);
                }
            }
        }
        &_note {
            //styleName: Body/Caption Regular;
            font-family: "Inter-Regular";
            font-size: 14px;
            font-weight: 400;
            line-height: 16.8px;
            text-align: left;
            color: var(--gray-dark);
            background-color: var(--gray-light);
            padding: 6px 8px;
            display: flex;
            align-items: center;
            border-radius: 4px;
            margin-top: 16px;
            margin-bottom: 24px;
            gap: 6px;
        }
        &_header {
            //styleName: Heading/H1;
            font-family: Poppins;
            font-size: 28px;
            font-weight: 500;
            line-height: 33.6px;
            text-align: center;
            color: var(--gray-dark);
            margin-bottom: 24px;
            height: 50px;
            display: flex;
            align-items: center;
        }
        &_description {
            //styleName: Body/Content Regular;
            font-family: "Inter-Regular";
            font-size: 16px;
            font-weight: 400;
            line-height: 19.2px;
            text-align: left;
            color: var(--gray-dark);
            margin-bottom: 15px;
            display: flex;
            align-items: center;
            gap: 5px;
            span {
                width: 30px;
                height: 24px;
                font-family: "Inter-Regular";
                font-size: 12px;
                font-weight: 400;
                line-height: 14.4px;
                letter-spacing: 0.01em;
                text-align: center;
                color: var(--blue-dark);
                background: var(--blue-light);
                display: flex;
                align-items: center;
                justify-content: center;
                border-radius: 2px;
            }
        }
        &_divider {
            width: 100%;
            min-height: 1px;
            background-color: var(--gray-light3);
        }
        &_close {
            position: absolute;
            top: 38px;
            right: 48px;
            cursor: pointer;
        }
        &_footer {
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 100%;
            margin-top: 12px;
            &_container {
                display: flex;
                gap: 46px;
            }
            &_support {
                display: flex;
                align-items: center;
                justify-content: center;
                gap: 8px;
                cursor: pointer;
                padding: 12px;
                //styleName: Heading/Heading 3;
                font-family: Poppins;
                font-size: 18px;
                font-weight: 500;
                line-height: 27px;
                text-align: center;
                color: var(--gray-dark);
            }
            &_price {
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                gap: 4px;
                &_text {
                    //styleName: Body/Caption Accent;
                    font-family: "Inter-Medium";
                    font-size: 14px;
                    font-weight: 600;
                    line-height: 16.8px;
                    text-align: left;
                    color: var(--black);
                }
                &_number {
                    //styleName: Numbers/Summaries Price;
                    font-family: Reddit Mono;
                    font-size: 20px;
                    font-weight: 600;
                    line-height: 20px;
                    letter-spacing: 0.02em;
                    text-align: left;
                    color: var(--blue-dark);
                    span {
                    }
                }
            }
            &_request {
                //styleName: Heading/Heading 3;
                font-family: Poppins;
                font-size: 18px;
                font-weight: 500;
                line-height: 27px;
                text-align: center;
                color: var(--white);
                background-color: var(--black);
                padding: 10px 18px;
                border-radius: 10px;
                cursor: pointer;
            }
        }
    }
}
.calendarInput {
    //styleName: Body/Form Title;
    font-family: "Inter-Medium";
    font-size: 14px;
    font-weight: 500;
    line-height: 16.8px;
    letter-spacing: 0.02em;
    text-align: left;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    cursor: pointer;
    position: relative;
    span {
        //styleName: Body/Form Title;
        font-family: "Inter-Medium";
        font-size: 14px;
        font-weight: 500;
        line-height: 16.8px;
        letter-spacing: 0.02em;
        text-align: left;
        color: var(--gray-light5);
    }
}
.calendarIcon {
    width: 48px;
    height: 44px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    outline: none;
    background-color: var(--green-light3);
    border-radius: 6px;
}
.scrollerArrow {
    position: absolute;
    top: 12.5px;
    right: 12px;
}
.react-calendar {
    width: 424px;
    min-height: 412px;
    border: 1px solid var(--gray-light3);
    border-radius: 16px;
    padding: 16px;
}
.react-calendar__navigation {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 10px;
    border-bottom: 1px solid var(--gray-light3);
}
.react-calendar__navigation__prev-button {
    width: 40px;
    height: 36px;
    max-width: 40px;
    max-height: 36px;
    background-color: transparent;
    border: 1px solid var(--gray-light3);
    border-radius: 4px;
}
.react-calendar__navigation__prev2-button {
    display: none;
}
.react-calendar__navigation__next2-button {
    display: none;
}
.react-calendar__navigation__label {
    //styleName: Heading/H3;
    font-family: Poppins;
    font-size: 18px;
    font-weight: 600;
    line-height: 27px;
    color: var(--black);
    height: 36px;
    background-color: transparent;
    border: none;
}
.react-calendar__navigation__next-button {
    width: 40px;
    height: 36px;
    max-width: 40px;
    max-height: 36px;
    background-color: transparent;
    border: 1px solid var(--gray-light3);
    border-radius: 4px;
}
.react-calendar__month-view__weekdays {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.react-calendar__month-view__weekdays__weekday {
    width: 48px;
    height: 48px;
    max-width: 48px;
    max-height: 48px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    //styleName: Heading/H4;
    font-family: Poppins;
    font-size: 16px;
    font-weight: 500;
    line-height: 17.6px;
    letter-spacing: 0.02em;
    text-align: left;
    color: var(--gray);
}
.react-calendar__month-view__weekdays__weekday abbr {
    border: none;
    text-decoration: none;
}
.react-calendar__month-view__days {
    justify-content: space-between;
}
.react-calendar__month-view__days__day {
    width: 56px;
    height: 56px;
    max-width: 56px;
    max-height: 56px;
    border: none;
    outline: none;
    //styleName: Text/Medium;
    font-family: "Inter-Medium";
    font-size: 16px;
    line-height: 19.2px;
    text-align: center;
    background-color: transparent;
    color: var(--gray-dark);
    display: flex;
    align-items: center;
    justify-content: center;
}
.react-calendar__month-view__days__day--weekend {
    position: relative;
    background-color: var(--blue-light);
}
.react-calendar__month-view__days__day--weekend::after {
    content: "+$";
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2px;
    font-family: "Inter-Medium";
    font-size: 10px;
    font-weight: 500;
    line-height: 12px;
    text-align: center;
    color: var(--blue-dark);
}
.pasted_day {
    font-family: "Inter-Regular";
    font-size: 16px;
    font-weight: 400;
    line-height: 19.2px;
    text-align: center;
    color: var(--gray);
    cursor: not-allowed !important;
    background-color: transparent;
}
.pasted_day::after {
    content: "";
    display: none;
}

.notAvailable_day {
    //styleName: Text/Medium;
    font-family: "Inter-Regular";
    font-size: 16px;
    font-weight: 400;
    line-height: 19.2px;
    text-align: center;
    color: var(--gray);
}
.react-calendar__tile--rangeStart abbr::before {
    content: "";
    display: none;
}
.react-calendar__tile--rangeStart abbr {
    //styleName: Text/Medium;
    font-family: "Inter-Regular";
    font-size: 16px;
    font-weight: 400;
    line-height: 19.2px;
    text-align: center;
    color: var(--white);
    background-color: var(--black);
    padding: 6px;
    border-radius: 50%;
    display: inline-block;
    width: 28px;
    height: 28px;
    font-family: "Poppins";
    font-weight: 700;
    letter-spacing: 0.02em;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}
.react-calendar__tile--now {
    position: relative;
}
.react-calendar__tile--now ::before {
    content: "";
    position: absolute;
    bottom: 3px;
    left: 50%;
    transform: translateX(-50%);
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: var(--blue-dark);
}
.next-seven-days {
    background-color: var(--blue-light);
    border-radius: 2px;
    position: relative;
}
.next-seven-days::after {
    content: "+$$";
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2px;
    font-family: "Inter-Medium";
    font-size: 10px;
    font-weight: 500;
    line-height: 12px;
    text-align: center;
    color: var(--blue-dark);
}
