.selectProfileСontainer {
    background-color: var(--gray-light);
    width: 100%;
    min-height: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    @media screen and (max-width: 1024px) {
        padding-bottom: 135px;
    }
    @media screen and (max-width: 640px) {
        padding-bottom: 82px;
    }
    &__content {
        background-color: var(--white);
        border-radius: 18px;

        width: 79%;
        max-width: 1137px;
        max-height: 709px;
        margin-top: 108px;
        flex: 1;
        display: flex;
        align-items: center;
        flex-direction: column;
        padding-top: 132px;
        position: relative;
        @media screen and (max-width: 1024px) {
            width: 88%;
            max-width: 734px;
            min-height: 925px;
            height: 925px;
            max-height: 925px;
            margin-top: 45px;
            padding-top: 176.6px;
        }
        @media screen and (max-width: 640px) {
            margin-top: 36px;
            padding: 16px;
            padding-top: 86px;
            height: unset;
            max-height: unset;
            min-height: 558px;
        }
        &_image {
            width: 506px;
            height: 89px;
            background-image: url(../../assets/images/SelectProfile/Welcome.png);
            background-repeat: no-repeat;
            background-position: center;
            background-size: 100% 100%;
            margin-bottom: 24px;
            @media screen and (max-width: 1024px) {
                width: 427px;
                max-width: 57.3%;
                background-size: contain;
                height: 74px;
                margin-bottom: 24px;
            }
            @media screen and (max-width: 640px) {
                width: 267px;
                max-width: 267px;
                min-height: 46px;
                height: 46px;
                background-position: center;
                background-size: 100% 100%;
            }
        }
        &_text {
            width: 601px;
            color: var(--gray-dark);
            text-align: center;
            font-family: "Inter-Regular";
            font-weight: 400;
            font-size: 20px;
            font-style: normal;
            font-weight: 400;
            line-height: 110%;
            margin-bottom: 36px;

            @media screen and (max-width: 1024px) {
                max-width: 81%;
                font-size: 16px;
                line-height: 140%;
            }
            @media screen and (max-width: 640px) {
                width: 100%;
                max-width: 295px;
                font-family: "Inter-Regular";
                font-weight: 400;
                font-size: 12px;
                line-height: 120%;
            }
        }
        &_accountsList {
            display: flex;
            width: 475px;
            align-items: center;
            justify-content: space-between;
            @media screen and (max-width: 1024px) {
                width: 256px;
                flex-wrap: wrap;
            }
            @media screen and (max-width: 640px) {
                width: 198px;
            }
            &_item {
                display: flex;
                flex-direction: column;
                align-items: center;
                width: 119px;
                color: var(--black);
                text-align: center;
                font-family: "Poppins";
                font-weight: 400;
                font-weight: 500;
                font-size: 20px;
                font-style: normal;
                font-weight: 400;
                line-height: 110%;
                &_foto {
                    width: 80px;
                    height: 80px;
                    margin-bottom: 16px;
                    cursor: pointer;
                }
                @media screen and (max-width: 1024px) {
                    width: 110px;
                    margin-bottom: 24px;
                    font-size: 18px;
                }
                @media screen and (max-width: 640px) {
                    font-size: 14px;
                    line-height: 140%;
                    width: 94px;
                    &_foto {
                        width: 64px;
                        height: 64px;
                        margin-bottom: 10px;
                    }
                }
            }
        }
        &_editButton {
            width: 104px;
            height: 37px;
            color: var(--black);
            text-align: center;
            font-family: "Inter-Regular";
            font-weight: 400;
            font-size: 20px;
            font-style: normal;
            font-weight: 400;
            line-height: 110%;
            border: 1px solid var(--gray-light3);
            border-radius: 32px;
            position: absolute;
            top: 20px;
            right: 20px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 10px 18.5px;
            cursor: pointer;
            @media screen and (max-width: 640px) {
                top: 16px;
                right: 16px;
                width: 81px;
                height: 34px;
                border-radius: 32px;
                padding: 6px 12px;
                font-family: "Poppins";
                font-weight: 400;
                font-size: 16px;
                line-height: 140%;
            }
        }
        &_addIcon {
            color: var(--black);
            text-align: center;
            font-family: "Poppins";
            font-weight: 400;
            font-weight: 500;
            font-size: 20px;
            font-style: normal;
            font-weight: 400;
            line-height: 110%;
            display: flex;
            align-items: center;
            flex-direction: column;
            margin-top: 36px;
            cursor: pointer;
            &_img {
                width: 56px;
                height: 56px;
                margin-bottom: 20px;
            }
            @media screen and (max-width: 640px) {
                margin-top: 0px;
                font-size: 18px;
                font-style: normal;
                font-weight: 400;
                line-height: 140%;
                &_img {
                    width: 36px;
                    height: 36px;
                    margin-bottom: 10px;
                }
            }
        }
    }
}
