                font-weight: 500;
                line-height: 13.2px;
                letter-spacing: 0.02em;
                text-align: left;
                color: var(--gray);
                margin-bottom: 12px;
            }
        }
        &_option {
            width: 100%;
            height: 44px;
            background-color: var(--green-light3);
            margin-bottom: 16px;
            border-radius: 4px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 0px 6px;

            &_text {
                //styleName: Heading/H4;
