                    border: 1px solid var(--gray-light3);
                    //styleName: Body/Form Title;
                    font-family: Inter;
                    font-size: 14px;
                    font-weight: 500;
                    line-height: 16.8px;
                    letter-spacing: 0.02em;
                    text-align: left;
                    display: flex;
                    align-items: center;
