            &_timeInput {
                flex: 1;
                display: flex;
                align-items: flex-start;
                flex-direction: column;
                gap: 4px;
                &_input {
                    width: 100%;
                    border-radius: 6px;
                    height: 44px;
                    border: 1px solid var(--gray-light3);
                }
            }
            &_buttons {
                display: flex;
                align-items: center;
                justify-content: center;
                gap: 12px;
                a {
                    text-decoration: none;
                }
                &_delete {
                    width: 48px;
                    height: 44px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    border: none;
                    outline: none;
                    background-color: var(--red-light);
                    border-radius: 6px;
                    text-decoration: none;
                }
                &_date {
                    width: 48px;
                    height: 44px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    border: none;
                    outline: none;
                    background-color: var(--green-light3);
                    border-radius: 6px;
                    text-decoration: none;
                }
            }
            &_note {
                //styleName: Body/Caption Regular;
                font-family: "Inter-Regular";
                font-size: 14px;
                font-weight: 400;
                line-height: 16.8px;
                text-align: left;
                color: var(--gray-dark);
                background-color: var(--gray-light);
                padding: 6px 8px;
                display: flex;
                align-items: center;
                border-radius: 4px;
                margin-top: 16px;
                margin-bottom: 0px;
                gap: 6px;
            }
            &_valid {
                position: absolute;
                top: 16px;
                left: 12px;
            }
        }
