    &_shotList {
        width: 100%;
        &_header {
            font-family: Poppins;
            font-size: 16px;
            font-weight: 500;
            line-height: 17.6px;
            letter-spacing: 0.02em;
            text-align: left;
            color: var(--black);
            margin-bottom: 12px;
            font-family: Poppins;
            font-size: 19px;
            font-weight: 600;
            line-height: 27px;
            text-align: left;
            color: var(--black);
        }
        &_editorContainer {
            width: 100%;
            height: 170px;
            border: 1px solid var(--gray-light3);
            border-radius: 12px;
        }
        &_iconDivider {
            margin-right: 30px !important;
            position: relative !important;
        }
        &_iconDivider::after {
            content: " ";
            background-color: var(--gray-light3) !important ;
            width: 1px !important;
            height: 15px;
            position: absolute;
            right: -15px;
        }
    }
}
.swiper_pagination {
