.footer {
    width: 100%;
    border-top: 1px solid rgba(220, 220, 220, 1);
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 12px;
    padding-bottom: 16px;
    font-family: "Inter-Regular";
  font-weight: 400;
    font-size: 16px;
    font-weight: 400;
    line-height: 15.4px;
    text-align: left;
    color: rgba(100, 100, 100, 1);
    white-space: nowrap;
    margin-top: 16px;
    @media screen and (max-width: 1120px) {
        flex-direction: column;
        justify-content: center;
        gap: 12px;
    }
    @media screen and (max-width: 768px) {
        border-top: none;
    }
    &__navigation {
        gap: 12px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media screen and (max-width: 768px) {
            flex-direction: column;
            display: none;
        }
        &_item {
            white-space: nowrap;
        }
    }
}
